        br
        h1 The Marine Biological Association 
        h1 16th Annual Postgraduate Conference
        h2 5th - 8th May 2019 &nbsp; &nbsp; · &nbsp; &nbsp; Newcastle University
        br
        //- v-btn(href='https://webstore.ncl.ac.uk/conferences-and-events/faculty-of-science-agriculture-engineering/school-of-natural-and-environmental-sciences/the-marine-biological-association-16th-annual-postgraduate-conference-2019' large color="base" dark) Buy tickets
