        br
        h1 Keynotes
        br
        h2 Dr Matt Frost <em>(Deputy Director, MBA)</em>
        p Dr Frost has a degree and PhD in marine benthic ecology but has since focused on marine policy; providing science to policy training; lecturing on postgraduate courses; chairing science-policy committees; and representing the Association in marine policy advice including responding to government consultations and engaging with parliament and policy groups more widely. He has published over 60 journal papers, reports and book chapters as well as writing articles and giving talks aimed at a broader audience (and serves on the editorial board for a number of marine journals). Matt is President of the European Network of Marine Research Institutes and Stations (MARS).
        h2 Professor Nick Polunin <em>(Newcastle University)</em>
        div
          //- img(src='./assets/nick.jpg' style="vertical-align:middle" id='logo')
          p A Marine Conservation Ecologist researching and lecturing ecosystem connectivity at Newcastle University, including reef fish-habitats and sustainable fisheries. Professor Polunin is also an editor for the Cambridge University Press and hugely involved with the Northumberland Inshore Fisheries and Conservation Authority (NIFCA).
        h2 Dr Amy Lusher <em>(Norwegian Institute for Water Research)</em>
        p A researcher in the field of marine pollution at the Norwegian Institute for Water Research, focusing on the distribution, interactions and effects of microplastics in the marine environment. Dr Lusher has developed novel methods for sampling microplastics from water, sediment and biota. Dr Lusher sits on several international working groups and advisory boards related to marine litter. She advocates for efficient communication between scientists and the public. Dr Lusher has been focusing her research on arctic environments.
        h2 Professor Claire Hellio <em>(University of Western Brittany)</em>
        div
          //- img(src='./assets/claire.jpg')
          p Professor of Marine Biotechnology at the University of Bretagne Occidentale and Director of Biodimar, a technological core facility providing expert services in marine biomolecule extraction, purification and analysis. Professor Hellio is also a representative of the European Society for Marine Biotechnology.
  v-container(fluid class="base")
