        br
        h2 Duncan Yellowlees
        p We are so pleased to host a fantastic communication workshop this year with the talented speaker, <a href='http://duncanyellowlees.com'> Duncan Yellowlees</a>. Helping us turn our ideas into engaging and interesting presentations, Duncan will train us in the art of communicating logically and emotionally, person to person. Forget boring presentations even existed! It’s time we get inspired! This workshop will take place in the afternoon of Monday 6th May 2019 at 16:30. 
        h2 Dave Skinner
        p Dave Skinner is a translator, editor and speechwriter. After years of wading through unnecessarily complicated technical and specialist material in several languages, he and colleagues started a Fight the Fog campaign consisting of guides and lectures on clear writing. On Tuesday 7th May, he will show you how to make your work easier to read and explain tips and techniques, backed up by exercises, to help you make your scientific documents more accessible to the general public.
