        h1 Frequently Asked Questions
        p <em>What is the closest metro stop to the Lindisfarne Room at Newcastle University?</em>
        p Haymarket and then approximately a 5 minute walk to Lindisfarne Room. 
        p <em>How do I reach the conference venue from Newcastle train station? </em>
        p Walk approximately 20 minutes or take the metro northbound to Haymarket and walk approximately 5 minutes. 
        p <em>How do I reach the conference venue after flying into Newcastle International Airport? </em>
        p There is a direct line from Newcastle International Airport to Haymarket which takes approximately 25 minutes. Alternatively, a taxi fare would cost around £15 and takes approximately 15 minutes. 
        p <em>Is there parking close to the conference venue?</em>
        p Yes, there are two car parks close, either Claremont Road Car Park NE2 4AN which would cost £1.30 per hour and is approximately a 6 minute walk from the Lindisfarne Room, or Eldon Gardens Multi-storey NE1 7RT which costs £1.80 and is also a 6 minute walk. 
        p <em>How do I find the conference venue upon my arrival? </em>
        p Lindisfarne Room is located on level 4 of the Hadrian Building at Newcastle University, which can be located on the website map or using campus maps all over the campus. The room can be reached by climbing the outdoor stairs which face the Bedson Building, where you will see our welcome banner. Committee members will be clearly identifiable, wearing blue committee t-shirts, so ask any of us for further directions if you see us around campus! 
        p <em>What is the best way to ask questions regarding my presentation? </em>
        p Email, tweet or Facebook message us with any questions or queries before the conference, and we will happily reply as soon as possible! Our email address is: mba2019@newcastle.ac.uk and all our social media details are on this website!
        p <em> How will I find the social event venues if I haven’t been to Newcastle before? </em>
        p The committee will be staying on campus after the days talks and workshops, so you’re welcome to chill with us and walk straight down to the evening locations. You will be given a digital map upon arrival or ask anyone in a blue committee t-shirt throughout the day events. Newcastle is a very friendly city so asking the locals is never a bad idea either, they can point you in the right direction. 
        p <em>Where is the best place to stay for a student in Newcastle? </em>
        p Newcastle is a relatively cheap city to visit, therefore there are plenty of options for student budgets! See our accommodation recommendations above, where we have quoted prices and walking distances. If you would like to discuss accommodation with us, just tweet or Facebook message us and we will happily help you decide where is best for you to stay. 
